import { Injectable } from '@angular/core';

@Injectable()
export class UsefulDataService {

  constructor() { }


  getMartialStatus(lang) {
    if (lang == 'fr') {
      return [{
        label: 'Sparation de biens',
        value: 'r0'
      }, {
        label: 'Communaut universelle',
        value: 'r1'
      }, {
        label: 'Participation aux acquts',
        value: 'r2'
      }]
    } else {
      return [{
        label: 'Separation of goods',
        value: 'r0'
      }, {
        label: 'Universal Community',
        value: 'r1'
      }, {
        label: 'Participation in acquests',
        value: 'r2'
      }]
    }
  }
  getProfitionalSituations(lang){
    if(lang == 'fr'){
      return [
        {label:'Agriculteurs exploitants',value:'PCS1'},
        {label:'Artisans, commerants et chefs dentreprise',value:'PCS2'},
        {label:'Cadres et professions intellectuelles suprieures',value:'PCS3'},
        {label:'Professions Intermdiaires',value:'PCS4'},
        {label:'Employs',value:'PCS5'},
        {label:'Ouvriers',value:'PCS6'},
        {label:'Retraits',value:'PCS7'},
        {label:'Autres personnes sans activit professionnelle',value:'PCS8'}
      ]
    }else{
      return [
         {label: 'Farmer', value: 'PCS1'},
 {label: 'Craftsmen, shopkeepers and entrepreneurs', value: 'PCS2'},
 {label: 'Executives and higher intellectual professions', value: 'PCS3'},
 {label: 'Intermediate Professions', value: 'PCS4'},
 {label: 'Employees', value: 'PCS5'},
 {label: 'Workers', value: 'PCS6'},
 {label: 'Retired', value: 'PCS7'},
 {label: 'Other people without professional activity', value: 'PCS8'}
      ]
    }
  }
  getOpeningHsbcAccountReason(lang){
    return [{label:'Raison 1',value:'r1'},{label:'Raison 2',value:'r2'},{label:'Raison 3',value:'r3'},{label:'Raison 4',value:'r4'}]
  }
  getResidanceStatus(lang){
    if(lang == 'fr'){
      return [
        {label:'Propritaire',value:'rs0'},
        {label:'Locataire',value:'rs1'}
      ]
    }else{
      return [
        {label:'Owner',value:'rs0'},
        {label:'Tenant',value:'rs1'}
      ]
    }
  }

  getFamilySituations(lang) {
    if (lang == 'fr') {
      return [{
        label: 'Clibataire',
        value: 'single'
      }, {
        label: 'Mari(e)',
        value: 'married'
      }, {
        label: 'Divorc(e)',
        value: 'divorced'
      }, {
        label: 'Veuf(ve)',
        value: 'widowed'
      }]

    } else {
      return [{
        label: 'Single',
        value: 'single'
      }, {
        label: 'Married',
        value: 'married'
      }, {
        label: 'Divorced',
        value: 'divorced'
      }, {
        label: 'Widowed',
        value: 'widowed'
      }]
    }
  }

  getLongNumbersName(lang){
    if(lang == 'fr'){
      return ["Seconde", "Troisieme"];
    }else{
      return ["Second","Third"]
    }
  }

}



